body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@font-face {
  font-family: 'Kanit';
  src: local('Kanit'), url('./fonts/Kanit/Kanit-Regular.ttf') format('truetype');

}

@font-face {
  font-family: 'Chakra_Petch';
  src: local('Chakra_Petch'), url('./fonts/Chakra_Petch/ChakraPetch-Regular.ttf') format('truetype');

}
@font-face {
  font-family: 'K2D';
  src: local('K2D'), url('./fonts/K2D/K2D-Regular.ttf') format('truetype');

}

@font-face {
  font-family: 'Prompt';
  src: local('Prompt'), url('./fonts/Prompt/Prompt-Regular.ttf') format('truetype');

}

@font-face {
  font-family: 'Sarabun';
  src: local('Sarabun'), url('./fonts/Sarabun/Sarabun-Regular.ttf') format('truetype');

}
@font-face {
  font-family: 'Lato';
  src: local('Lato'), url('./fonts/Lato-Regular.ttf') format('truetype');
}

