.App {
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  height: 60vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-profile1 {
    animation: App-logo-spin infinite 5s linear;
  }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(255, 255, 255);
}

.App-Paper {
  background-color: #fffbbf;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(255, 255, 255);
  padding: 10px;
}

.App-profileA {
  height: 30vh;
  pointer-events: none;
  margin: 0;
  border-radius: 50%;
  margin: 10px 10px;
}

.App-profile {
  height: 30vh;
  position: relative;
  pointer-events: none;
  margin: 0;
  border-radius: 50%;
  border: 1vh solid;
  margin: 10px 10px;
  border-color: rgba(144, 240, 255, 0.8);
  box-shadow: 4px 3px rgba(144, 240, 255, 0.5);
}
.App-profile1 {
  position: absolute;
  left: calc(50vw - 17.5vmin);
  width: 27vh;
  color: aquamarine;
  min-width: 27vh;
  pointer-events: none;
  margin: 0;
  border-radius: 50%;
  border: 1vh solid;
  margin: 10px 10px;
  border-color: rgba(255, 173, 119, 0.8);
  box-shadow: 2px 2px rgba(175, 87, 28, 0.8);
}

.flash1 {
  color: rgb(173, 255, 244);
}
.flash2 {
  color: rgb(255, 210, 173);
}
.flash3 {
  color: rgb(255, 173, 173);
}
a:link {
  text-decoration: none;
}

  /* visited link */
  a:visited {
    text-decoration: none;
  
  }

  /* mouse over link */
  a:hover {
    color: rgb(185, 255, 195);
    text-decoration: none;
  
  }

  /* selected link */
  a:active {
    color: rgb(80, 80, 80);
  }

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes FadeAnimation {
  0% {
    opacity: 1;
    visibility: visible;
  }

  100% {
    opacity: 0;
    visibility: hidden;
  }
}

.aws-btn {
  --button-default-height: 48px;
  --button-default-font-size: 14px;
  --button-default-border-radius: 6px;
  --button-horizontal-padding: 20px;
  --button-raise-level: 5px;
  --button-hover-pressure: 2;
  --transform-speed: 0.185s;
  --button-primary-color: #475472;
  --button-primary-color-dark: #2a3143;
  --button-primary-color-light: #d4d9e4;
  --button-primary-color-hover: #424e6a;
  --button-primary-border: none;
  --button-secondary-color: #fffc6c;
  --button-secondary-color-dark: #b9b500;
  --button-secondary-color-light: #6c6a00;
  --button-secondary-color-hover: #fffb3e;
  --button-secondary-border: none;
  --button-anchor-color: #f3c8ad;
  --button-anchor-color-dark: #734922;
  --button-anchor-color-light: #4c3016;
  --button-anchor-color-hover: #f1bfa0;
  --button-anchor-border: 3px solid #8c633c;
  }

  .map-selected {
    fill: #E3DA37;
  }
  
  .map-unselected {
    fill: #699EAA;
  }
  
  .map-selected:hover, .map-unselected:hover {
    cursor: pointer;
  }